.lib-gen-con--wrap {
  margin: 0;

  h1 {
    margin-bottom: $space-3xl;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }
}

// .lib-gen-con--page-title {
// }

.lib-gen-con--block {
  max-width: rem(850);
  margin-bottom: $space-4xl;

  .lib-block-title {
    display: inline-block;
  }

  p + h3 {
    margin-top: $space-lg;
  }

  p {
    margin-top: 0;
  }

  & p + p:last-of-type {
    margin-bottom: 0;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.lib-img-block {
  @include clearfix;
}

.lib-text-img-right {
  .lib-gen-con--img-wrap {
    float: right;
    margin-left: $space-lg;

    @include media-breakpoint-only(xs) {
      float: initial;
      max-width: 100%;
      margin: 0 auto;
    }
  }
}

.lib-text-img-left {
  .lib-gen-con--img-wrap {
    float: left;
    margin-right: $space-lg;

    @include media-breakpoint-only(xs) {
      float: initial;
      max-width: 100%;
      margin: 0 auto;
    }
  }
}

.lib-block--lg-img {
  .lib-gen-con--img-wrap {
    max-width: rem(850);

    figure {
      position: relative;
      padding-top: percentage(9/16);
    }
  }
}

.lib-block--two-img {
  .lib-gen-con--img-wrap {
    max-width: rem(400);

    @include media-breakpoint-down(md) {
      max-width: 45%;
    }

    @include media-breakpoint-only(xs) {
      max-width: 100%;
    }
  }
}

.lib-block--three-img {
  .lib-gen-con--img-wrap {
    max-width: rem(250);

    @include media-breakpoint-down(md) {
      max-width: 32%;
    }

    @include media-breakpoint-only(xs) {
      max-width: 100%;
    }
  }
}

.lib-block--two-img,
.lib-block--three-img {
  .lib-content-wrap {
    display: flex;
    justify-content: space-around;
    @include media-breakpoint-only(xs) {
      flex-direction: column;
    }
  }
}

.lib-gen-con--img-wrap {
  max-width: rem(300);
  width: 100%;

  figure {
    position: relative;
    padding-top: percentage(400/400);
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    max-width: 100%;
    height: auto;
    box-shadow: $vert-dark-2;
  }

  figcaption {
    padding-top: $space-sm;
    text-align: center;
    font-style: italic;
  }
}

.lib-block--link-list {
  // box-sizing: content-box;
  .lib-link-list {
    display: flex;
    flex-wrap: wrap;
    margin: $space-2xl 0 0 0;

    @include media-breakpoint-only(xs) {
      margin: $space-lg 0 0 $space-md;
    }
  }

  .lib-link-list--item {
    position: relative;
    width: 50%;
    margin-bottom: $space-md;

    @include media-breakpoint-only(xs) {
      width: 100%;
      padding: $space-xs 0;
    }

    a {
      font-family: $heading-font;
      font-weight: 400;
      padding-left: $space-2xl;
      color: $link-color;
      text-decoration: none;
      transition: 0.3s ease;

      &::before {
        @include vert_center();
        font-family: 'FontAwesome';
        content: '\f15c';
        font-size: $fs-lg;
        left: 0;
        color: inherit;
      }

      &:hover,
      &:focus {
        color: $link-hover-color;
      }
    }
    /*
 |--------------------------------------------------------------------------
 | Changes icon based on file type
 |--------------------------------------------------------------------------
*/
    $doc-types: (
      '.docx': '\f1c2',
      '.pdf': '\f1c1',
      '.xlsx': '\f1c3',
      '.jpg': '\f1c5',
      '.png': '\f1c5',
      '.com': '\f35d',
      '.net': '\f35d',
      '.edu': '\f35d',
      '.org': '\f35d',
    );

    @each $docType, $icon in $doc-types {
      @if "a[href*='#{$docType}']" {
        a[href*='#{$docType}']::before {
          content: $icon;
        }
      }
    }
  }
}

.lib-link-list--abstract {
  margin: $space-2xl 0 0 0;

  .lib-abstract-item {
    position: relative;
    margin-bottom: $space-lg;
    padding-left: $space-2xl;

    &::before {
      @include vert_center(0);
      font-family: 'FontAwesome';
      content: '\f35d';
      // font-size: $fs-md;
      left: 0;
      color: $primary-color;
    }

    &:last-of-type {
      margin-bottom: 0;
    }

    h4 {
      line-height: 1.5;
      color: inherit;
      display: inline-block;
    }

    a {
      color: $link-color;
      text-decoration: none;
      transition: 0.3s ease;

      &:hover,
      &:focus {
        color: $link-hover-color;
      }
    }
  }
}

.lib-quote-block {
  position: relative;

  padding: $space-2xl 0 0;
  text-align: center;
}

.lib-block--quote {
}

.lib-block--quote-alt {
  padding: $space-2xl 0 $space-3xl;
  background-color: $warm-grey-100;

  .lib-quote {
    max-width: rem(637.5);

    @include media-breakpoint-only(xs) {
      max-width: 90%;
    }
  }
}

.lib-quote--marks {
  display: inline-block;
  position: relative;
  margin: 0 auto;
  width: rem(32);
  height: rem(32);
  border-radius: 100%;
  background-color: $warm-grey-300;

  i {
    @include absolute-center;
    position: absolute;
    text-align: center;
  }
}

.lib-block--quote-ribbon {
  padding: $space-2xl 0 $space-2xl;
  background-color: $warm-grey-100;
  box-shadow: $vert-light-2;
  // border-radius: $br4;
  border-left: solid 4px $warm-grey-600;
  quotes: '' '';

  &::before,
  &::after {
    position: absolute;
    top: 12%;
    font-family: $heading-font;
    font-size: rem(102);
    line-height: 1;
    color: $warm-grey-300;

    @include media-breakpoint-only(xs) {
      font-size: $fs-3xl;
    }
  }

  &::before {
    content: open-quote;
    left: 5%;

    @include media-breakpoint-only(xs) {
      left: 2%;
    }
  }

  &::after {
    content: close-quote;
    right: 5%;

    @include media-breakpoint-only(xs) {
      right: 2%;
    }
  }

  .lib-quote {
    @include media-breakpoint-only(xs) {
      max-width: 90%;
    }
  }
}

.lib-quote {
  max-width: 75%;
  font-family: $heading-font;
  font-style: italic;
  font-size: $fs-lg;
  margin: 0 auto;

  @include media-breakpoint-only(xs) {
    max-width: 95%;
  }

  cite {
    display: block;
    position: relative;
    font-style: initial;
    margin: 0 auto;
    padding: $space-sm $space-sm 0;
    color: $warm-grey-600;

    &::before,
    &::after {
      content: '\2014';
      color: $warm-grey-700;
      padding: 0 $space-sm;
    }

    &::before {
      left: -10%;
    }

    &::after {
      right: -10%;
    }
  }
}
